<template lang="pug">
.curso-main-container.pb-3
  BannerInterno
  .container.tarjeta.tarjeta--blanca.p-4.p-md-5.mb-5
    .titulo-principal
      .titulo-principal__numero
        span 2
      h1 Técnicas de negociación

    figure
      img(src="@/assets/curso/tema2/1.svg" data-aos="fade-up")

    .col-lg-11.m-auto
      .p-4(style="background: #f6f4fe;background: linear-gradient(59deg, #f6f4fe 0%, #dedede 5%, white 52%, #dedede 74%, #c9c9c9 98%);border-radius:7px;box-shadow: 0px 0px 20px 1px rgba(0,0,0,0.07);position:relative;margin-top:-50px;")
        p(data-aos="fade-up") Las técnicas de negociación son un conjunto de estrategias que podrán ser utilizadas dentro de una negociación y te permitirán lograr los objetivos esperados. La negociación no es un proceso sencillo, requiere de gran atención y aprender a empatizar con las expectativas de ambas partes. Sin embargo, es clave asumir una posición cooperativa y no generar conflicto para llegar a acuerdos efectivos.
        p.mt-3(data-aos="fade-up") A continuación, se presentan algunas técnicas de negociación que podrán ser útiles para aplicar en tu vida personal y profesional.

    Separador

    .titulo-segundo.mt-5
      h2#t_2_1 2.1 Mapa de la negociación

    p(data-aos="fade-up") Cada individuo elabora un mapa o modelo del mundo que es único, como los rasgos faciales o el ADN. Cada persona ve las cosas a su manera. Como lo hemos evidenciado en la negociación, no puede haber dos mapas idénticos, siempre van a haber discrepancias. El ideal sería que las personas pudieran estar de acuerdo en todo, pero la realidad es que esto nunca pasa.  Aunque, todos tienen algo en común con otras personas como el idioma, las costumbres, los valores, o los gustos.

    .row.mt-5
      .col-lg-5
        figure
          img(src="@/assets/curso/tema2/2.png" data-aos="fade-right")
      .col-lg-7
        p(data-aos="fade-up") #[strong Los conflictos se generan cuando:]
        ul.mt-3(style="list-style: disc;padding:revert;")
          li Las diferencias se interpretan como una amenaza. 
          li Se asumen las posiciones como oposiciones y no como complementos del otro.  
          li Algunas de las personas involucradas insisten en imponer su mapa mental como la realidad.
        p.mt-4(data-aos="fade-up") #[strong Debido a varios factores es imposibles percibir la realidad tal cual como es, los seres humanos solo pueden interpretarla: ]
        ul.mt-3(style="padding:revert;")
          li(data-aos="fade-up") #[strong(style="color:#0284C6 !important;") a)] La información recibida siempre tiene filtros personales como creencias, valores, intereses, etc.
          li(data-aos="fade-up") #[strong(style="color:#0284C6 !important;") b)] Se ha comprobado que las emociones intensas pueden cambiar la percepción de la realidad.
          li(data-aos="fade-up") #[strong(style="color:#0284C6 !important;") c)] La  memoria es selectiva, por lo que tiende a recordar los hechos e información que resulta acorde con el punto de vista personal.
        .cajon.color-acento-botones.p-4.mt-3
          p(data-aos="fade-up") De esa manera, cada persona construye un “mapa mental y emocional” que puede influir altamente en una negociación. Ya que el comportamiento estará condicionado por el mapa, por lo que es importante reconocer que cada persona tiene su propio y único mapa, conociendo esto podrá indagar para conocer gradualmente el mapa de su contraparte e identificar los puntos en común.  (Sanchéz, 2013)

    br
    .p-5.mt-5.cont_3(style="position:relative;")
      h5(data-aos="fade-up") Mapa de negociación y ejemplo aplicado
      p.mt-3(data-aos="fade-up") Las posiciones más claras para construir un mapa de negociación se pueden ver en los conflictos políticos o que implican decisiones ligadas a diferentes factores.
      p.mt-4(data-aos="fade-up") Es claro que, si construyes un mapa de los bandos de la guerra fría, uno para las URSS y otro para USA, encontrarás grandes diferencias. Esto puede parecer un poco complejo por la cantidad de intereses que interfieren en este proceso.

    p.mt-5.col-lg-9.text-center(style="color:#0284C6;font-weight:bold;" data-aos="fade-up") Ahora, puedes aterrizar el ejercicio a un ambiente laboral en el que se está presentando una diferencia entre un colaborador y su jefe, donde identificará las siguientes situaciones en los mapas de cada una de las partes.

    div.cont_4(style="position:relative;z-index:1;")
      .row
        .col-lg-7(style="place-self:center;")
          h5(data-aos="fade-up") Situación desde la perspectiva de Juan
          p.mt-3(data-aos="fade-up") Juan, el colaborador, siente bastante presión pues tiene mucho trabajo represado y no ha logrado ponerse al día. Al parecer su jefe, no contempla todas las tareas que ya tiene y sigue entregando más responsabilidades dando plazo de entrega para todo su trabajo, el lunes de la próxima semana. #[br] Juan ha identificado que hay personas que podrían apoyarlo, pero no tiene forma de involucrarlos sin que su jefe dé la instrucción.  Juan, considera que por más que trabaje horas extras no podrá cumplir con las responsabilidades ya que trabaja solo en estas labores. Adicionalmente, tiene que cumplir con las labores de su hogar, apoyar a su esposa y a sus hijos. #[br] Últimamente, ha tenido un dolor muy fuerte de cabeza que reduce su eficiencia en el trabajo.
        .col-lg-5
          figure
            img(src="@/assets/curso/tema2/4.png" data-aos="fade-left")

      .row
        .col-lg-5
          figure
            img(src="@/assets/curso/tema2/5.png" data-aos="fade-right")
        .col-lg-7(style="place-self:center;")
          h5(data-aos="fade-up") Situación desde la perspectiva de Álvaro
          p.mt-3(data-aos="fade-up") Álvaro, el jefe, siente bastante presión, pues el director comercial, su jefe, considera que el equipo de trabajo de Álvaro no tiene suficiente compromiso. En repetidas ocasiones el director ha amenazado a Álvaro con cambiar a todo el equipo incluyéndolo a él. Álvaro está muy preocupado porque adquirió un crédito para comprar su nueva vivienda y sin el trabajo no podría seguir asumiendo esa responsabilidad.
          p.mt-3(data-aos="fade-up") A pesar de que el equipo de Álvaro es bastante grande, él no tiene claras las tareas que le asigna a cada persona, claramente confía más en ciertas personas y hay otras que tienen demasiado tiempo libre.  Álvaro siempre le ha costado delegar, en realidad su único interés es que pueda cumplir con los compromisos que adquirió con las directivas.

    .cajon.color-acento-botones.p-4.mt-5.col-lg-10.m-auto
      .row
        .col-lg-10
          p(data-aos="fade-up") Al leer las dos posiciones, podrás construir un mapa de negociación para cada uno, como se evidencia:
          ul.mt-3(style="padding:revert;")
            li(data-aos="fade-up") #[strong 1.] Las creencias de cada uno son diferentes.
            li(data-aos="fade-up") #[strong 2.] Tienen una perspectiva de la realidad distinta. 
            li(data-aos="fade-up") #[strong 3.] Las preocupaciones de cada uno están ligadas a los compromisos laborales y personales. 
            li(data-aos="fade-up") #[strong 4.] Los dos desean cumplir con sus objetivos. 
            li(data-aos="fade-up") #[strong 5.] Álvaro no quiere que ninguno pierda su trabajo. 
            li(data-aos="fade-up") #[strong 6.] Juan necesita apoyo que le pueden dar otras personas de la empresa.
        .col-lg-2(style="place-self:center")
          figure
            img(src="@/assets/curso/tema2/7.svg" data-aos="fade-right" style="max-width:100px;")

    Separador

    .titulo-segundo.mt-5
      h2#t_2_2 2.2 Estilos de negociación
    .cont_5.pb-5(style="position:relative;z-index:1;")
      p(data-aos="fade-up") A partir de la combinación de dos variables fundamentales en la resolución de conflictos se determina el perfil negociador y el enfoque que desarrolla el individuo en las situaciones de tensión con otras personas (Sanchéz, 2013). Dichas variables son:

      .row.mt-4.col-lg-8.m-auto
        .col-md-6
          .tarjeta.tarjeta-flip.color-primario
            .indicador--hover
            .tarjeta-flip__contenedor
              .tarjeta-flip__img(:style="{'background-image': `url(${require('@/assets/curso/tema2/8.svg')})`}")
              .tarjeta-flip__contenido.p-5
                h3.text-center Asertividad
                p.text-center Es el grado en que una persona busca satisfacer y reconocer sus intereses propios.
        .col-md-6
          .tarjeta.tarjeta-flip.color-primario
            .indicador--hover
            .tarjeta-flip__contenedor
              .tarjeta-flip__img(:style="{'background-image': `url(${require('@/assets/curso/tema2/9.svg')})`}")
              .tarjeta-flip__contenido.p-5
                h3.text-center Cooperación
                p.text-center Es el grado en que una persona intenta satisfacer los intereses de la otra persona.

    br
    ImagenInfografica.color-secundario.mt-5.col-lg-8.m-auto(style="position:relative;z-index:1")
      template(v-slot:imagen)
        figure
          img(src='@/assets/curso/tema2/10.svg', alt='')

      .tarjeta.color-acento-botones.p-3(x="40%" y="7%" numero="1")
        p #[strong Lobo (competitivo):] es un perfil muy asertivo y poco cooperativo. Son personas orientadas hacia el poder, el cual busca satisfacer sus propios intereses. Buscan defender sus derechos y posturas. Se enfocan en los resultados y las metas en donde es importante generar cierto control.
      .tarjeta.color-acento-botones.p-3(x="90%" y="7%" numero="2")
        p #[strong Conejo (colaborativo):] estilo tanto asertivo como cooperativo. Son personas que buscan trabajar juntamente con la otra parte para encontrar una solución para ambos. Antes de confrontar buscan explorar las posiciones y los compromisos, así como generar alternativas favorables para todos. Están orientados a las personas y a generar entusiasmo promoviendo la creatividad en la generación de soluciones.
      .tarjeta.color-acento-botones.p-3(x="49%" y="64%" numero="3")
        p #[strong Búho (equitativo):] es un perfil intermedio de asertivo y cooperativo. Son personas que buscan generar soluciones oportunas y aceptables que satisfagan parcialmente a las partes. Tienden a ceder siendo menos competitivos sin ser complacientes. No son evasivos, confrontan de forma directa los asuntos. Se basan en datos o hechos puntuales que les permitan determinar qué es lo justo.
      .tarjeta.color-acento-botones.p-3(x="58%" y="91%" numero="4")
        p #[strong Oveja (adaptativo):] es un perfil que tiende a ser muy cooperativo y poco asertivo. Son personas que dejan de lado sus propios intereses para satisfacer a los demás. Pueden ser generosos o altruistas, hasta el punto del autosacrificio, restando fuerza a su opinión para complacer a otros. Están orientados al equipo y al servicio. Para estas personas es más importante la relación que el objetivo.
      .tarjeta.color-acento-botones.p-3(x="40%" y="91%" numero="5")
        p #[strong Avestruz (evasivo):] es un perfil que no es ni asertivo, ni cooperativo. Son personas que no buscan inicialmente sus intereses ni los de la otra persona, no se dirigen al conflicto. Prefieren aplazar, eludir o apartarse de situaciones amenazantes. Buscan no actuar, ni lidiar con el conflicto. Son conservadores y evasivos.


  
